/* General Page Styles */
.post-item-page {
  font-family: 'Arial', sans-serif;
  background-color: #f9f9f9;
  padding: 20px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #333;
}

.post-item-page h2 {
  color: #4CAF50;
  margin-bottom: 20px;
  text-align: center;
  font-size: 2rem;
}

/* Form Container */
.post-item-form {
  background-color: #fff;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 500px;
}

.post-item-form label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
  font-size: 0.9rem;
  color: #555;
}

.post-item-form input,
.post-item-form select,
.post-item-form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 0.9rem;
  font-family: 'Arial', sans-serif;
  box-sizing: border-box;
  transition: border-color 0.3s ease;
}

.post-item-form input:focus,
.post-item-form select:focus,
.post-item-form textarea:focus {
  border-color: #4CAF50;
  outline: none;
}

.post-item-form textarea {
  resize: none;
  height: 100px;
}

/* Image Upload */
.image-upload-container {
  text-align: center;
  margin-bottom: 20px;
}

.image-upload-container input[type="file"] {
  display: none;
}

.image-upload-container label {
  display: inline-block;
  padding: 10px 20px;
  background-color: #4CAF50;
  color: #fff;
  font-size: 0.9rem;
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.image-upload-container label:hover {
  background-color: #45a049;
}

/* Submit Button */
.post-item-form button {
  width: 100%;
  padding: 12px;
  background-color: #4CAF50;
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.post-item-form button:hover {
  background-color: #45a049;
}

/* Responsive Design */
@media (max-width: 768px) {
  .post-item-page {
    padding: 15px;
  }

  .post-item-form {
    padding: 20px;
  }

  .post-item-page h2 {
    font-size: 1.8rem;
  }
}
